@import '../StyleVariables.scss';
::-webkit-scrollbar {
  width: 6px;
  border-left: 1px solid #e6ecf8;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
}

body {
  font-family: 'SLight';

  div {
    overflow: hidden;
  }

  p,
  span,
  h1,
  h2 {
    animation: fadeIn 1s;
  }

  h1,
  h2 {
    font-family: 'SRegular';
  }

  p {
    font-size: 1.3rem;
  }

  h2 {
    font-size: 2rem;
  }

  @media (max-width: $breakpoint-mobile) {
    p {
      font-size: 1.1rem;
    }
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
